import React, { Component } from 'react';
import { render } from 'react-dom';
import Hello from './Hello';
import './style.css';

class App extends Component {
  constructor() {
    super();
    this.state = {
      movies: [], 
      input: ''
    };
  }


  componentWillMount() {
    this.fetch('Avengers')
  }

  fetch(t){
    fetch(`https://www.omdbapi.com/?s=${t}&plot=full&apikey=796d37c3`)
      .then(response => response.json())
      .then(data => {
        console.log(data)
        this.setState({
          movies: data.Search,
        })
        }
      )
      .catch(error => this.setState({ error, isLoading: false }));
  }

  handleAddTodo = () => {
    console.log('===== handleAddTodo() =====')
    let input = this.state.input;
    this.setState({
      movies: [],
    });
    this.fetch(input)
    this.setState({
      input: '',
    });
  }

  handleChangeTodo = (e) => {
    console.log('===== handleChangeTodo() =====')
    this.setState({
      input: e.target.value
    })
  }

  render() {
    let movies = this.state.movies
    let input = this.state.input
    console.log(movies.length)
    
    return (
      <div>
        <Hello name={this.state.name} />

        <div>
          <input type="text" value={input} onChange={this.handleChangeTodo}/>
          <button onClick={this.handleAddTodo}>Search</button>
        </div>
        {movies.length ? (
          movies.map((movie, i) => (
            <div className='card' key={i}>
              <div>
                <h2>{movie.Title}</h2>
                <p>{movie.Year}</p>
                <p>{movie.Type}</p>
              </div>
              <div>
                <img src={movie.Poster} />
              </div>
            </div>
          ))
        ) : (
          <div>Loading ...</div>
        )}   
         
      </div>
    );
  }
}

render(<App />, document.getElementById('root'));
